// import logo from "./logo.svg";
import { useState, useEffect, useRef } from "react";
import "./App.scss";

export default function App() {
  //29/29 
  //=============INIT================================
  const [isRunning, setIsRunning] = useState(false);
  const [stage, setStage] = useState("session");
  const [sessionTime, setSessionTime] = useState(25);
  const [breakTime, setBreakTime] = useState(5);
  const [timeLeft, setTimeLeft] = useState(
    () => (stage === "session" ? sessionTime : breakTime) * 60
  );

  //============AUDIO=================================
  const audioRef = useRef();
  function playSound() {
    if (audioRef.current !== null) {
      audioRef.current.play();
    }
  }
  function resetSound() {
    audioRef.current.pause();
    audioRef.current.currentTime = 0;
  }

  // ==========FORMATTING/CONVERTING HELPERS============
  const minToSec = (min) => min * 60;
  const formatTime = (secRaw) => {
    let min = (secRaw / 60) ^ 0;
    let sec = secRaw % 60 ^ 0;
    min = min < 10 ? `0${min}` : min;
    sec = sec < 10 ? `0${sec}` : sec;
    return `${min}:${sec}`;
  };

  // =======SETTINGS HANDLERS===========================
  //settings handlers refactor this!
  const increaseSessionTime = () => {
    if (sessionTime < 60 && !isRunning) {
      setSessionTime((prevSessionTime) => prevSessionTime + 1);
      setTimeLeft((prevTimeLeft) => prevTimeLeft + 60);
    } else {
      return false;
    }
  };
  const decreaseSessionTime = () => {
    if (sessionTime > 1 && !isRunning) {
      setSessionTime((prevSessionTime) => prevSessionTime - 1);
      setTimeLeft((prevTimeLeft) => prevTimeLeft - 60);
    } else {
      return false;
    }
  };
  const increaseBreakTime = () => {
    if (breakTime < 60 && !isRunning) {
      setBreakTime((prevBreakTime) => prevBreakTime + 1);
    } else {
      return false;
    }
  };
  const decreaseBreakTime = () => {
    if (breakTime > 1 && !isRunning) {
      setBreakTime((prevBreakTime) => prevBreakTime - 1);
    } else {
      return false;
    }
  };

  // BUTTONS HANDLERS=======================
  const reset = () => {
    setStage("session");
    setSessionTime(25);
    setBreakTime(5);
    setTimeLeft(() => minToSec(25));
    setIsRunning(false);
    resetSound();
  };

  const startPause = (e) => setIsRunning((prev) => !prev);

  // ==========TIMER================================
  useEffect(() => {
    let timer = null;
    if (timeLeft === 0) {
      setStage((prevStage) => (prevStage === "session" ? "break" : "session"));
      setTimeLeft(() =>
        minToSec(stage === "session" ? breakTime : sessionTime)
      );

      playSound();
      return () => {
        clearInterval(timer);
      };
    }

    if (isRunning) {
      // set interval
      timer = setInterval(() => {
        setTimeLeft((prevTimeLeft) => prevTimeLeft - 1);
      }, 1000);
    } else if (!isRunning && timeLeft !== 0) {
      //this   part is for pausing
      clearInterval(timer);
    }
    return () => clearInterval(timer);
  }, [breakTime, isRunning, sessionTime, stage, timeLeft]);

  return (
    <>
      <div className="header">
        <h1 className="title">25 + 5 Clock</h1>
      </div>
      <div className="timer">
        <div className="timer__info">
          <div id="timer-label">{stage}</div>

          <div id="time-left">{formatTime(timeLeft)}</div>
        </div>
        <div className="timer__buttons">
          <button id="start_stop" className="btn" onClick={startPause}>
            {isRunning ? "pause" : "start"}
          </button>
          <button id="reset" className="btn" onClick={reset}>
            reset
          </button>
        </div>
      </div>
      <div className="settings">
        <div className="settings_header">
          <h3>Settings</h3>
        </div>
        <div className={`session`}>
          <div id={`session-label`}>session</div>
          <div className={`settings-wrap`}>
            <button id={`session-increment`} onClick={increaseSessionTime}>
              +
            </button>
            <div id={`session-length`}>{sessionTime}</div>
            <button id={`session-decrement`} onClick={decreaseSessionTime}>
              -
            </button>
          </div>
        </div>
        <div className={`break`}>
          <div id={`break-label`}>break</div>
          <div className={`settings-wrap`}>
            <button id={`break-increment`} onClick={increaseBreakTime}>
              +
            </button>
            <div id={`break-length`}>{breakTime}</div>
            <button id={`break-decrement`} onClick={decreaseBreakTime}>
              -
            </button>
          </div>
        </div>
      </div>
      <audio
        id="beep"
        ref={(el) => (audioRef.current = el)}
        src={"https://www.soundjay.com/nature/water-dripping-1.mp3"}
      />
    </>
  );
}
